// HOC例子
import React from "react";
import ReactDom from "react-dom";
import PropTypes from "prop-types";
import { ZcodeHighlight, ZpageWraperHOC, ZerodMainContext, Zlayout, zTool, ZfullLayer } from "zerod";
import cssClass from "./style.scss";
//兼容ie10的dataset属性
function getDataset(element) {
	var obj = {};
	if (element.dataset) {
		return element.dataset;
	} else {
		for (var i = 0; i < element.attributes.length; i++) {
			var key = element.attributes[i].nodeName;
			if (/^data-\w+$/.test(key)) {
				//判断是否以data-开头的属性名
				var value = element.attributes[i].nodeValue; //值
				var keyName = key.match(/^data-(\w+)/)[1]; //键名
				obj[keyName] = value; //对象添加属性
			}
		}
	}
	return obj;
}
const ZpageWraper = ZpageWraperHOC();
let hasUnmounted = false;
function AmdDocHOC(doc, renders) {
	doc =
		`<p>如带有 <i class="zero-icon zerod-shengchangzhouqi"></i> 标记的 “函数” 表示会在组件的生命周期函数 “render” 中执行</p>` +
		doc;
	class Doc extends React.PureComponent {
		renderEls = [];
		componentWillUnmount() {
			hasUnmounted = true;
			this.renderEls.forEach((el) => {
				ReactDom.unmountComponentAtNode(el);
			});
			if (this.scrollInstance && this.scrollInstance.scroll)
				this.scrollInstance.scroll.off("scrollEnd", this.scrollEnd);
		}
		state = {
			navs: [],
			closeModaled: true,
		};
		setNavs = () => {
			const _navs = [];
			const _docTitles = this.mdEl.querySelectorAll(".z-doc-titles");
			// console.dir(_docTitles);
			Array.prototype.slice.call(_docTitles).forEach((el) => {
				_navs.push({
					name: el.dataset.title ? el.dataset.title : el.nextElementSibling.innerText,
					el: el.nextElementSibling,
					active: -this.scrollInstance.scroll.y == el.nextElementSibling.offsetTop - 24,
				});
			});
			if (_navs.length) {
				this.setState(
					{
						navs: _navs,
					},
					() => {
						this.bindScrollEvent();

						if (this.navEl && this.navContentEl) {
							const { BuildScroll } = zTool;
							this.navScroollInstance = new BuildScroll(this.navEl, { scrollbars: "custom" });
							zTool.listenDivSizeChange(this.navEl, this.navScroollInstance.refresh);
							zTool.listenDivSizeChange(this.navContentEl, this.navScroollInstance.refresh);
						}
					},
				);
			}
		};
		scrollEnd = () => {
			const y = -this.scrollInstance.scroll.y;
			this.state.navs.forEach((item) => {
				item.active = false;
			});
			const len = this.state.navs.length;
			for (let i = 0; i < len; i++) {
				const item = this.state.navs[i];
				if (y >= item.el.offsetTop - 120) {
					if (i < len - 1) {
						const nextItem = this.state.navs[i + 1];
						if (nextItem && y < nextItem.el.offsetTop - 120) {
							item.active = true;
							break;
						}
					} else {
						item.active = true;
						break;
					}
				}
			}
			this.setState({
				navs: [...this.state.navs],
			});
		};
		bindScrollEvent = () => {
			this.scrollInstance.scroll.on("scrollEnd", this.scrollEnd);
		};
		navScrollTo = (item) => {
			this.scrollInstance.scroll.scrollTo(0, -(item.el.offsetTop - 24), 200);
		};
		componentDidMount() {
			const _allDemoBox = this.mdEl.querySelectorAll(".z-demo-box");
			Array.prototype.slice.call(_allDemoBox).forEach((el) => {
				const _dataset = getDataset(el);
				if (renders && typeof renders[_dataset.render] == "function") {
					let pre = el.nextElementSibling;
					pre = pre && pre.localName == "pre" ? pre : null;
					const Content = renders[_dataset.render]();
					this.renderEls.push(el);
					ReactDom.render(Content, el, () => {
						const _div = document.createElement("div");
						_div.className = "z-demo-footer";
						const _divcodebtn = document.createElement("div");
						_divcodebtn.className = "z-demo-code-btn z-flex-space-between";
						const _divcode = document.createElement("div");
						_divcode.className = "z-demo-code";
						const _img = document.createElement("img");
						_img.addEventListener(
							"click",
							() => {
								_img.src = _img.open
									? "https://gw.alipayobjects.com/zos/rmsportal/wSAkBuJFbdxsosKKpqyq.svg"
									: "https://gw.alipayobjects.com/zos/rmsportal/OpROPHYqWmrMDBFMZtKF.svg";
								_divcode.style.height = _img.open ? "0px" : "auto";
								_img.open = !_img.open;
							},
							false,
						);
						_img.src = "https://gw.alipayobjects.com/zos/rmsportal/wSAkBuJFbdxsosKKpqyq.svg";
						const _title = document.createElement("span");
						_title.innerText = _dataset.title ? _dataset.title : "";

						_divcodebtn.appendChild(_title);
						pre && _divcodebtn.appendChild(_img);
						_div.appendChild(_divcodebtn);
						pre && _divcode.appendChild(pre);
						_div.appendChild(_divcode);
						el.appendChild(_div);
					});
				}
			});

			this.mdEl.addEventListener(
				"click",
				(e) => {
					if (typeof e.target.className === "string" && e.target.className.includes("z-open-modal-btn")) {
						setTimeout(() => {
							hasUnmounted = false;
							if (typeof e.target._render == "function") {
								const contents = e.target._render();
								if (contents.windowType != "layer") {
									this.props.showRightModal({
										show: true,
										modal: e.target._modal ? e.target._modal : "mainModal",
										content: contents,
										scroll: e.target._scroll,
										onTransitionend: (show) => {
											if (!hasUnmounted)
												this.setState({
													closeModaled: !show,
												});
										},
									});
								} else {
									const { header, content } = contents;
									this.layerRef.current.methods.setHeader(header);
									this.layerRef.current.methods.setContent(content);
									this.layerRef.current.layerMethods.showLayer(true, null, true)();
								}
							}
						}, 10);
					}
				},
				false,
			);
			this.mdEl.addEventListener("click", (e) => {
				if (typeof e.target.className === "string" && e.target.className.includes("z-show-loading-btn")) {
					setTimeout(() => {
						if (e.target._modal == "mainRoute") {
							this.props.showRouteLoading(true);
							setTimeout(() => {
								this.props.showRouteLoading(false);
							}, 2000);
						} else {
							const modal = e.target._modal ? e.target._modal : "mainModal";
							hasUnmounted = false;
							this.props.showRightModal({
								show: true,
								modal,
								content: null,
								scroll: true,
								onTransitionend: (show) => {
									if (!hasUnmounted)
										this.setState({
											closeModaled: !show,
										});
								},
							});
							this.props.showModalLoading(true, modal);
							setTimeout(() => {
								this.props.showModalLoading(false, modal);
							}, 2000);
						}
					}, 10);
				} else if (typeof e.target.className === "string" && e.target.className.includes("z-history-href")) {
					const path = e.target.dataset.path;
					if (typeof path === "string") {
						this.props.$router.history.push(path);
					}
				}
			});
			this.scrollInstance = this.props.getScrollInstance("mainRoute");
			setTimeout(() => {
				this.setNavs();
			}, 100);
		}
		layerRef = React.createRef();
		render() {
			return (
				<Zlayout.Template>
					<ZpageWraper pageHeader={{ show: false }} hasBodyPadding={false}>
						<div className={`z-panel ${cssClass["z-md-doc"]}`} ref={(el) => (this.mdEl = el)}>
							<div
								className="z-panel-body"
								style={this.state.navs.length ? { width: "calc(100% - 136px)" } : {}}
							>
								<ZcodeHighlight mode="html">{doc}</ZcodeHighlight>
							</div>
						</div>
					</ZpageWraper>
					{this.state.navs.length && this.state.closeModaled
						? ReactDom.createPortal(
								<div className={cssClass["z-nav-box"]} ref={(el) => (this.navEl = el)}>
									<div ref={(el) => (this.navContentEl = el)}>
										<section>
											{this.state.navs.map((item, i) => {
												return (
													<div
														title={item.name}
														className={`${cssClass["z-nav"]} ${
															item.active ? cssClass["active"] : ""
														}`}
														key={i}
														onClick={() => {
															this.navScrollTo(item);
														}}
													>
														{item.name}
													</div>
												);
											})}
										</section>
									</div>
								</div>,
								document.body,
						  )
						: null}
					<Layer ref={this.layerRef} />
				</Zlayout.Template>
			);
		}
	}
	return ZerodMainContext.setConsumer(Doc);
}
class Layer extends React.PureComponent {
	methods = {
		setHeader: (c) => {
			this.setState({
				layerHeader: c,
			});
		},
		setContent: (c) => {
			this.setState({
				layerContent: c,
			});
		},
	};
	state = {
		layerHeader: null,
		layerContent: null,
	};
	getMethods = (m) => {
		this.layerMethods = m;
	};
	render() {
		const { layerHeader, layerContent } = this.state;
		return (
			<ZfullLayer header={layerHeader} exportMethods={this.getMethods}>
				{layerContent}
			</ZfullLayer>
		);
	}
}

export default {
	name: "AmdDocHOC",
	HOC: AmdDocHOC,
};
