import React from "react";
import { Button } from "antd";

function AshowDemoHOC(content, scroll) {
	class MyScript extends React.PureComponent {
		componentDidMount() {
			const _btn = this.boxEl.querySelector(".z-open-modal-btn");
			_btn._scroll = scroll;
			_btn._render = () => {
				return content;
			};
		}
		render() {
			return (
				<div
					ref={(el) => {
						this.boxEl = el;
					}}
				>
					<Button type="primary" className="z-open-modal-btn">
						查看Demo
					</Button>
				</div>
			);
		}
	}
	return MyScript;
}

export default {
	name: "AshowDemoHOC",
	HOC: AshowDemoHOC,
};
